import React, { useContext, useEffect, useState } from 'react';

import { useTranslation } from 'react-i18next';

import WebContext from '../../../application/contexts/WebContext';
import Global from '../../../infrastructure/Common/Global';
import ClientApis from '../APIs/ClientApis';

import DataNotFound from '../../../infrastructure/Common/DataNotFound';
import BlogCard from '../Shared/BlogCard';

const ArticlesList = () => {
    const { t } = useTranslation();
    const webCtx = useContext(WebContext);

    const [isLoading, setIsLoading] = useState(true);
    const [posts, setPosts] = useState([]);

    const baseURL = Global.baseUrl + "/";


    useEffect(() => {
        getBlog();
    }, []);

    const getBlog = () => {
        ClientApis.blog()
            .then(res => {
                // setTimeout(() => {
                setPosts(res.data?.result?.posts);
                setIsLoading(false);
                // }, 3000);
            })
            .catch(err => {
                console.log(err);
                setIsLoading(false);
            })
    };

    return (
        <div className="container-xxl py-5">

            <div className="d-block d-md-flex align-items-end justify-content-between">
                <div className="section-header">
                    {/* <div className="primary">{t('menu_courses')}</div> */}
                    <h2 className="title fw-medium mb-0">{t('blog_articles_title')}</h2>
                    <div>{t('blog_articles_desc')}</div>
                </div>
            </div>

            <section>
                <div className="section-content mt-5">
                    <div className="row">
                        {isLoading ?
                            Array.from({ length: 2 }, (_, index) => (
                                <div className="col-md-6 mb-4 mt-2" key={index}>
                                    <div className="home-blog p-2 d-flex flex-column flex-md-row w-100">
                                        <div className="content-placeholder w-100 p-3">
                                            <p className="card-text placeholder-glow">
                                                <span className="placeholder col-3"></span>
                                                <span className="placeholder col-12 mt-2"></span>
                                                <span className="placeholder col-9 mt-3"></span>
                                                <span className="placeholder col-7"></span>
                                                <span className="placeholder col-6"></span>
                                            </p>
                                        </div>
                                        <h5 className="w-225 min-h-250 placeholder-glow ms-3 mb-0">
                                            <span className="placeholder col-12 min-h-250"></span>
                                        </h5>
                                    </div>
                                </div>
                            ))
                            :
                            <>
                                {posts && posts.length ?
                                    <>
                                        {posts.map((item, index) => (
                                            <div className="col-md-6 mb-3" key={index}>
                                                <BlogCard data={item} appLang={webCtx.appLang} baseURL={baseURL} />
                                            </div>
                                        ))}
                                    </>

                                    : null}
                            </>
                        }
                        {!isLoading && !posts.length && <DataNotFound />}


                    </div>

                </div>
            </section>

        </div>
    );
};

export default ArticlesList;