import React, { useContext, useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useParams } from 'react-router-dom';
import * as moment from 'moment';

import WebContext from '../../../application/contexts/WebContext';
import Global from '../../../infrastructure/Common/Global';
import ClientApis from '../APIs/ClientApis';

import './blogs-n-articles.scss';

const Article = () => {
    const { t } = useTranslation();
    const webCtx = useContext(WebContext);
    const params = useParams();


    const [isLoading, setIsLoading] = useState(true);
    const [article, setArticle] = useState({});

    const baseURL = Global.baseUrl + "/";

    useEffect(() => {
        if (params.articleId) {
            getArticle(params.articleId);
        }
    }, []);

    const getArticle = (articleId) => {
        const param = { post_id: articleId }
        ClientApis.blogPost(param)
            .then(res => {
                // setTimeout(() => {
                setArticle(res.data.result);
                setIsLoading(false);
                // }, 4000);
            })
            .catch(err => {
                console.log(err);
                setIsLoading(false);
            })
    };

    return (
        <div className="">

            <section className="blog-meta py-5">
                <div className="container-xxl">
                    <div className="row">
                        <div className="col-md-8" data-aos="fade-up" data-aos-duration={500} data-aos-easing="linear" data-aos-delay={500}>
                            {isLoading ?
                                <div className="content-placeholder">
                                    <h1 className="placeholder-glow mb-4">
                                        <span className="placeholder col-11 h-25"></span>
                                        <span className="placeholder col-5 mt-3"></span>
                                    </h1>
                                    <p className="card-text placeholder-glow">
                                        <span className="placeholder col-12 mt-3"></span>
                                        <span className="placeholder col-6"></span>
                                        <br />
                                        <span className="placeholder col-5 my-3"></span>
                                        <br />
                                        <span className="placeholder col-4"></span>

                                    </p>
                                </div>
                                :
                                <React.Fragment>
                                    <h1 className="title fw-medium mb-0">{article['title_' + webCtx.appLang]}</h1>
                                    <div className="my-3">{article['short_desc_' + webCtx.appLang]}</div>

                                    <div className="my-3">
                                        <span className="max-text">{t('posted')}:</span> {article.created_at ? moment(article.created_at).format('MMMM DD, YYYY') : ''}
                                    </div>

                                    {article.sub_type && 
                                        <div className="my-3">
                                            <span className="max-text">{t('category')}:</span> {t(article.sub_type)}
                                        </div>
                                    }
                                    
                                    <div className="my-3">
                                        <span className="max-text">{t('author')}:</span> <span className="underline fw-medium">{article.author}</span>
                                    </div>

                                </React.Fragment>
                            }
                        </div>
                        <div className="col-md-4" data-aos="fade-up" data-aos-duration={500} data-aos-easing="linear" data-aos-delay={1000}>
                            <div className="img-container">

                                {isLoading ?
                                    <div className="content-placeholder">
                                        <h5 className="w-100 min-h-200 placeholder-glow mt-2 me-3">
                                            <span className="placeholder col-12 min-h-250"></span>
                                        </h5>
                                    </div>
                                    : <div className="blog-img" style={{ backgroundImage: `url('${baseURL + article.featured_url}')` }}></div>}

                            </div>
                        </div>
                    </div>
                </div>
            </section>

            <section className="py-5">
                <div className="container-xxl">
                    <div className="" data-aos="fade-up" data-aos-duration={500} data-aos-easing="linear">
                        <div className="row">
                            <div className="col-md-8">
                                {/* <div dangerouslySetInnerHTML={{ __html: article[`about_${webCtx.appLang}`] }} /> */}
                                {article[`content_${webCtx.appLang}`]}
                            </div>
                        </div>
                    </div>

                </div>
            </section>

        </div>
    );
};

export default Article;